/* eslint-disable no-alert */
<template>
  <h2>add edit dele item</h2>
  <p>{{ temp }}</p>
  <AdminNav />
  <!-- <router-link to="/admin-products">Products Database</router-link> | -->
  <div class="container">
    <table class="table">
      <thead>
        <tr>
          <th style="vertical-align: middle" scope="col"></th>
          <th style="vertical-align: middle" scope="col">品名</th>
          <th style="vertical-align: middle" scope="col">分類</th>
          <th style="vertical-align: middle" scope="col">原價</th>
          <th style="vertical-align: middle" scope="col">折扣價</th>
          <th style="vertical-align: middle" scope="col">上架</th>
          <th style="vertical-align: middle" scope="col">
            <button @click="managerProductBtn('add')" type="button" class="btn btn-outline-primary">
              add item
            </button>
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="product in products" :key="product.id">
          <th scope="row">
            <div>
              <img
                :style="{ backgroundImage: `url(${product.imageUrl})` }"
                style="
                  height: 80px;
                  width: 80px;
                  background-size: cover;
                  background-position: center center;
                "
                alt=""
              />
            </div>
          </th>
          <td style="vertical-align: middle">{{ product.title }}</td>
          <td style="vertical-align: middle">{{ product.category }}</td>
          <td style="vertical-align: middle">{{ product.origin_price }}</td>
          <td style="vertical-align: middle">{{ product.price }}</td>
          <td style="vertical-align: middle">
            <div class="form-check form-switch">
              <input
                v-model="product.is_enabled"
                :true-value="1"
                :false-value="0"
                class="form-check-input"
                type="checkbox"
                id="flexSwitchCheckChecked"
                checked
              />
              <label class="form-check-label" for="flexSwitchCheckChecked"></label>
            </div>
          </td>
          <td style="vertical-align: middle">
            <div class="btn-group">
              <button
                @click="managerProductBtn('edit', product)"
                type="button"
                class="btn btn-outline-warning"
              >
                edit
              </button>
              <button
                @click="managerProductBtn('delete', product.id)"
                type="button"
                class="btn btn-outline-danger"
              >
                delete
              </button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
    <Pages @to-page="getProductsPage" :pages="pagination" />
  </div>
  <!-- add edit -->
  <AdminProductsModal
    :target="temp.data"
    :modalType="type"
    @emit-temp="getTempData"
    @emit-modal="getModal"
    @emit-confirm-btn="getTypeAndAxios"
  />
</template>

<script>
import Pages from '@/components/Pagination.vue';
import AdminNav from '@/components/AdminNav.vue';
import AdminProductsModal from '@/components/AdminProductsModal.vue';

const url = process.env.VUE_APP_URL;
const path = process.env.VUE_APP_PATH;
const baseURL = `${url}api/${path}/admin/product`;

export default {
  components: {
    Pages,
    AdminNav,
    AdminProductsModal,
  },
  data() {
    return {
      products: [],
      pagination: {},
      modal: '',
      type: '',
      targetID: '',
      axiosUrl: '',
      temp: {
        data: {},
      },
    };
  },
  methods: {
    checkUser() {
      this.$http
        .post(`${url}api/user/check`)
        .then((res) => {
          // console.log(res);
          console.log('login success: ', res.data.success);
        })
        .catch((err) => {
          console.dir(err);
          this.$router.push('/');
        });
    },
    getProductsPage(page = 1) {
      this.$http
        .get(`${url}api/${path}/admin/products?page=${page}`)
        .then((res) => {
          // console.log(res);
          const { products, pagination } = res.data;
          this.products = products;
          this.pagination = pagination;
        })
        .catch((err) => {
          console.dir(err);
        });
    },
    // === mitt to axios ===
    managerProductBtn(type, obj) {
      if (type === 'add') {
        this.type = 'add';
        this.openModal();
      } else if (type === 'edit') {
        this.type = 'edit';
        this.temp.data = obj;
        this.openModal();
      } else if (type === 'delete') {
        this.targetID = obj.id;
        this.type = 'delete';
        this.mittCRUD();
      }
    },
    mittCRUD() {
      let method = '';
      const obj = JSON.parse(JSON.stringify(this.temp));
      if (this.type === 'add') {
        method = 'post';
        this.axiosUrl = baseURL;
      } else if (this.type === 'edit') {
        method = 'put';
        this.axiosUrl = `${baseURL}/${this.tempID}`;
      } else if (this.type === 'delete') {
        method = 'delete';
        this.axiosUrl = `${baseURL}/${this.tempID}`;
      }

      this.$http[method](this.axiosUrl, obj)
        .then((res) => {
          alert(res.data.message);
          this.modal.hide();
          this.getProductsPage();
          this.axiosUrl = '';
          this.tempID = '';
          this.temp = { data: {} };
        })
        .catch((err) => {
          console.dir(err);
        });
    },
    // === mitt ===
    // get checkout modal & open modal
    getModal(obj) {
      this.modal = obj;
    },
    // modal 確認後 取得 temp data & execute axios
    getTempData(obj) {
      this.temp.data = obj;
    },
    //  get method type of axios
    getTypeAndAxios(obj) {
      this.type = obj;
      this.mittCRUD();
    },
    openModal() {
      this.modal.show();
    },
    closeModal() {
      this.getProductsPage();
      this.modal.hide();
    },
    // === mitt ===
    // 讀取效果
    isLoading() {
      const loader = this.$loading.show({
        canCancel: true,
        onCancel: this.onCancel,
      });
      // simulate AJAX
      setTimeout(() => {
        loader.hide();
      }, 500);
    },
    onCancel() {
      console.log('User cancelled the loader.');
    },
  },
  mounted() {
    this.isLoading();
    const myCookie = document.cookie.replace(/(?:(?:^|.*;\s*)myToken\s*=\s*([^;]*).*$)|^.*$/, '$1');
    this.$http.defaults.headers.common.Authorization = myCookie;
    this.checkUser();
    this.getProductsPage();
  },
};
</script>
