<script>
import Modal from 'bootstrap/js/dist/modal';

// const url = process.env.VUE_APP_URL;
// const path = process.env.VUE_APP_PATH;
// const baseURL = `${url}api/${path}/admin/product`;

export default {
  props: ['modalType', 'target'],
  name: 'AdminProductsModal',
  data() {
    return {
      modal: '',
      type: '',
      newID: '',
      temp: {
        imageUrl: '',
        imagesUrl: [],
        title: '',
        is_enabled: false,
        category: '',
        unit: '',
        origin_price: null,
        price: null,
        description: '',
        content: '',
      },
    };
  },
  watch: {
    target(n, o) {
      console.log(' new target: ', n, ' , old target: ', o);
      this.temp = this.target;
    },
    modalType(n, o) {
      console.log(' new modalType: ', n, ' , old modalType: ', o);
      this.type = this.modalType;
      if (this.type === 'add') {
        this.temp = '';
      }
    },
  },
  methods: {
    emitConfirmBtn(type) {
      if (type === 'add') {
        this.type = 'add';
      } else if (type === 'edit') {
        this.type = 'edit';
      }
      this.$emit('emit-temp', this.temp);
      this.$emit('emit-confirm-btn', this.type);
    },
    // 新增 編輯商品視窗
    emitModal() {
      this.$emit('emit-modal', this.modal);
    },
  },
  mounted() {
    this.modal = new Modal(this.$refs.modal);
    this.emitModal();
  },
};
</script>
